* {
  padding: 0;
  margin: 0;
}
::-webkit-scrollbar {
  width: 10px;
  padding: 5px;
}

::-webkit-scrollbar-track {
  border-radius: 10px;
  box-shadow: inset 0 0 10px rgba(180, 180, 180, 0.15);
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #bdbfc2;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #878888;
}
.navbar {
  width: 100%;
  height: 80px;
  background-color: #0d253f;
  display: flex;
  justify-content: space-around;
}
.navbar-leftside {
  display: flex;
  align-content: center;
  justify-content: space-between;
  align-items: center;
  width: 40%;
}
.navbar-rightside {
  width: 60%;
  display: flex;
  justify-content: flex-end;
}
.navbar img {
  height: 30px;
  padding: 25px
  
}    
.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 50vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav-links {
  color: white;
  text-decoration: none;
  padding: 0.5rem 1rem;
  font-size: 1em;
}
.nav-links:hover {
  color: #4b9fff;
  transition: .4s;
  cursor: pointer;
}

.nav-links-mobile {
  display: none;
}
.menu-btn {
  position: relative;
  justify-content: center;
  display: none;
  align-items: center;
  height: 80px;
  width: 80px;
  cursor: pointer;
  transition: all .5s ease-in-out;
  /* border: 3px solid white; */
}
.menu-btn_burger {
  height: 4px;
  width: 40px;
  background: white;
  border-radius: 5px;
  transition: all .5s ease-in-out;
}
.menu-btn_burger::before,
.menu-btn_burger::after {
  content: " ";
  position: absolute;
  width: 40px;
  height: 4px;
  background: white;
  border-radius: 5px;
  transition: all .5s ease-in-out;
}
.menu-btn_burger::before {
  transform: translateY(-10px);
}
.menu-btn_burger::after {
  transform: translateY(10px);
}

/* Animation */
.menu-btn.open .menu-btn_burger {
  transform: translateX(-50px);
  background: transparent;
}
.menu-btn.open .menu-btn_burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-btn_burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}
.top-container {
  background-image: linear-gradient(to right, #0d253f, #01b4e4);
  background-repeat:inherit;
  background-size: cover;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
  
}

.welcome-h1 {
  margin-top: 20px;
  font-size: 2em;
  padding-left: 30px;
  padding-right: 30px;

  
}
.top-container h2 {
  font-size: 1.2em;
