/* Popular Movie Styles */
.popular-movies-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}
.popular-movies-h2 {
  
}
.popular-movies-h2 h2 {
  text-align: left;
  padding: 20px;
}
.movies-container {
  display: flex;
  overflow: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 45px;
  width: 100%;
}
.movie-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 15%;
}
.movie-image {
  width: 100%;
  display: contents;
}
.movie-image img {
  width: 85%;
  border-radius: 10px;
  box-shadow: 3px 3px rgb(0, 0, 0, .1);
}
.movie-info {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}
.movie-info h4 {
  padding-bottom: 10px;
  width: 90%;
}
footer p {
  text-align: center;
  padding: 20px;
}
.movie-info a {
  text-decoration: none;
  color: black;
}

/* Individual Movie Styles */

.individual-movie-container {
  width: 100%;
  margin-top: 50px;
  height: auto;
}

.individual-movie-container img {
  width: 100%;
}


